@font-face {
  font-family: "DroidSans";
  font-weight: 400;
  font-style: normal;
  src: url("/src/assets/fonts/droidsansregular.woff") format("woff"), url("/src/assets/fonts/droidsansregular.woff2") format("woff2"); }

@font-face {
  font-family: "DroidSans";
  font-weight: 700;
  font-style: normal;
  src: url("/src/assets/fonts/droidsansbold.woff") format("woff"), url("/src/assets/fonts/droidsansbold.woff2") format("woff2"); }

body {
  min-width: 1020px;
