.page-header {
  box-sizing: border-box;
  height: 360px; }

.page-header__image-wrapper {
  position: relative;
  display: flex;
  justify-content: flex-end; }

.page-header__image-wrapper img {
  margin-top: -38px;
  margin-right: -40px; }

.page-header::before {
  content: "";
  position: absolute;
  top: 134px;
  right: 0;
  z-index: -1;
  width: 100%;
  height: 4px;
  background-color: #3F3F3F;
  box-shadow: 0 14px 0 0 #3F3F3F, 0 28px 0 0 #3F3F3F, 0 42px 0 0 #3F3F3F, 0 56px 0 0 #3F3F3F, 0 70px 0 0 #3F3F3F; }

.page-main {
  margin-top: -40px; }

.page-main__title {
  margin: 0 0 20px 0;
  padding-top: 5px;
  font-size: 22px;
  letter-spacing: 0.9px; }

.page-footer {
  position: relative;
  padding-top: 80px; }

.page-footer__image-wrapper {
  position: absolute;
  width: 1020px;
  top: -5px;
  z-index: -1; }

.page-footer__image-wrapper img {
  margin-left: -40px; }

.page-footer::before {
  content: "";
  position: absolute;
  top: 64px;
  right: 0;
  width: 100%;
  height: 6px;
  background-color: #3F3F3F; }

.page-footer__content-wrapper {
  background-color: #3F3F3F; }

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 60px 0; }

.page-footer__column {
  width: 118px; }

.page-footer__logo img {
  fill: #ffffff; }

.page-footer__social {
  display: flex;
  justify-content: space-between;
  margin: 53px 0 0 0;
  padding: 0;
  list-style-type: none; }

.page-footer__links {
  display: flex;
  width: 700px;
  margin: 20px 0 0 0; }

.main-nav {
  background-color: rgba(30, 9, 6, 0.15); }

.main-nav__wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 110px; }

.main-nav__list {
  margin-left: 115px; }

.container {
  box-sizing: border-box;
  width: 1020px;
  margin: 0 auto;
  padding: 0 40px; }

.user-list {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
  width: 131px;
  margin-left: auto;
  padding: 0 10px 0 0;
  list-style-type: none; }

.user-list__item {
  display: flex; }
  .user-list__item--basket span {
    margin-top: -5px;
    margin-left: 2px; }
  .user-list__item--basket span::after {
    content: attr(data-title);
    display: inline-block;
    top: 10px;
    font-size: 10px;
    font-weight: 400;
    line-height: 12px;
    color: #F20505; }

.site-list {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  list-style-type: none; }

.site-list__item {
  margin-right: 40px; }

.site-list__item a {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 1px;
  color: #000000; }

.links__wrapper {
  box-sizing: border-box;
  margin-right: 14px; }

.links__wrapper:last-child {
  margin-right: 0;
  width: 140px;
  margin-left: auto; }

.links__wrapper dt {
  margin-bottom: 11px;
  font-size: 14px;
  color: #ffffff;
  letter-spacing: 2px; }

.links__wrapper:first-child dt {
  margin-bottom: 17px; }

.links__wrapper:last-child dt {
  margin-bottom: 15px; }

.links dd {
  margin: 0; }

.links__list {
  margin: 0;
  padding: 0;
  list-style-type: none; }
  .links__list--about-us, .links__list--kontakt {
    width: 145px;
    line-height: 12px;
    letter-spacing: 0.3px; }
  .links__list--kontakt {
    width: 130px; }
  .links__list--about-us .links__item:first-child,
  .links__list--kontakt .links__item:first-child {
    margin-bottom: 12px; }
  .links__list--catalog {
    width: 150px; }
  .links__list--catalog .links__item,
  .links__list--info .links__item {
    line-height: 18px; }
  .links__list--catalog .links__item,
  .links__list--info .links__item,
  .links__list--kontakt .links__item {
    letter-spacing: 1.3px; }
  .links__list--kontakt .links__item a {
    padding-left: 14px;
    letter-spacing: 1px; }
  .links__list--kontakt .links__item a::before {
    content: "";
    position: absolute;
    display: block;
    top: 38px;
    left: 0;
    width: 10px;
    height: 10px;
    background-image: url("../img/icon_phone.svg"); }
  .links__list--kontakt .links__item span::before {
    content: "";
    position: absolute;
    top: 11px;
    left: 0;
    width: 10px;
    height: 10px;
    background-image: url("../img/icon_clock.svg"); }

.links__item {
  position: relative; }

.links__item a,
.links__item p,
.links__item span {
  margin: 0;
  color: #ffffff;
  font-size: 10px; }

.links__item span {
  display: block;
  padding-left: 14px;
  margin-top: -1px; }

.catalog__content-wrapper {
  display: flex;
  margin-top: 50px; }

.catalog__filters-column {
  width: 208px; }

.catalog__filters-column h2 {
  margin: 0;
  padding-bottom: 30px;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: .9px;
  border-bottom: 0.7px solid #DDDADA; }

.catalog__filters-form fieldset {
  margin: 0;
  padding: 0 0 30px 0;
  border: none;
  border-bottom: 0.7px solid #DDDADA; }

.catalog__filters-form h3 {
  margin: 30px 0 25px 0;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: .9px; }

.catalog__filters-price-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between; }

.catalog__filters-price-wrapper input {
  box-sizing: border-box;
  width: 84px;
  height: 28px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px;
  text-align: center;
  border: 0.7px solid #9F9E9E;
  border-radius: 2px; }

.catalog__filters-price-wrapper::after {
  content: "";
  position: absolute;
  display: block;
  top: 44%;
  right: 50%;
  margin-right: -6px;
  width: 14px;
  height: 1px;
  background-color: #585757; }

.catalog__filters-type-wrapper {
  display: flex;
  flex-direction: column; }

.catalog__filters-type-content-wrapper {
  position: relative;
  margin-bottom: 18px; }

.catalog__filters-type-content-wrapper label {
  padding-left: 41px;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: .4px;
  cursor: pointer;
  user-select: none; }

.catalog__filters-type-content-wrapper input + label::before {
  content: "";
  position: absolute;
  left: 0px;
  top: -2px;
  width: 22px;
  height: 22px;
  border: 0.7px solid #9F9E9E; }

.catalog__filters-type-content-wrapper input:checked + label::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  width: 12px;
  height: 12px;
  background-image: url("/src/assets/img/icon_check-mark-button.svg");
  background-repeat: no-repeat;
  background-position: 0 0; }

.catalog__sort {
  margin: 0;
  padding: 3px 0 0 33px;
  letter-spacing: 1.1px; }

.catalog__pagination {
  display: flex;
  list-style-type: none; }

.sort {
  display: flex;
  list-style-type: none; }

.sort__item:first-child {
  margin-left: 21px; }

.sort__item {
  margin-right: 34px; }

.breadcrumbs {
  display: flex;
  margin: 0;
  padding: 0;
  color: #474747;
  list-style-type: none; }

.breadcrumbs__item {
  position: relative;
  margin-right: 34px; }

.breadcrumbs__item::before {
  content: "";
  position: absolute;
  display: block;
  top: 52%;
  right: -22px;
  width: 12px;
  height: 1px;
  background-color: #000000;
  pointer-events: none; }

.breadcrumbs__item::after {
  content: "";
  position: absolute;
  display: block;
  top: 36%;
  right: -22px;
  border: solid black;
  border-width: 0 1px 1px 0;
  padding: 3px;
  transform: rotate(-45deg);
  pointer-events: none; }

.breadcrumbs__item--current::after,
.breadcrumbs__item--current::before {
  display: none; }

.breadcrumbs__item a {
  color: #474747;
  letter-spacing: 0.9px; }
